::placeholder {
  color: #ddf0f1;
}

[type=checkbox] {
  display: none;
}
[type=checkbox] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 1.5625rem;
  font-size: 1rem;
  line-height: 1;
  cursor: pointer;
  transition: 0.3s ease;
}
[type=checkbox] + span::before {
  position: absolute;
  content: "";
  top: 28%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #408f95;
  width: 0.875rem;
  height: 0.875rem;
  display: block;
}
@media screen and (min-width: 768px) {
  [type=checkbox] + span::before {
    left: 0;
  }
}
[type=checkbox] + span::after {
  opacity: 0;
  transition: 0.3s ease;
}
[type=checkbox]:checked + span::after {
  content: "";
  position: absolute;
  top: 15%;
  left: 1.25%;
  display: block;
  opacity: 1;
  /*チェックマークじゃない場合は以下を変更する*/
  transform: translateY(-50%) rotate(-45deg);
  width: 0.5625rem;
  height: 0.3125rem;
  border-left: 2px solid #c94800;
  border-bottom: 2px solid #c94800;
}
@media screen and (min-width: 768px) {
  [type=checkbox]:checked + span::after {
    top: 18%;
    left: 1%;
  }
}

[type=tel] {
  letter-spacing: -0.1em;
}

[type=checkbox] + span:not(:first-child) {
  margin-top: 0.375rem;
}
@media screen and (min-width: 768px) {
