.common-btn {
  color: #fff;
  font-size: 0.875rem;
  display: inline-block;
  text-align: center;
  cursor: pointer;
  padding: 1rem 3.9375rem;
  position: relative;
  z-index: 1;
  background-color: #408f95;
  border: 0.0625rem solid #408f95;
}

.common-btn::first-letter {
  text-transform: capitalize;
}

/*影の設定*/
.common-btn:before {
  content: "";
  /*絶対配置で影の位置を決める*/
  position: absolute;
  z-index: -1;
  top: 0.25rem;
  left: 0.25rem;
  /*影の形状*/
  width: 100%;
  height: 100%;
  border-radius: 0;
  border: 0.0625rem solid #408f95;
}

.common-btn::after {
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
}

.common-btn:hover::after {
  transform: scale(1, 1);
  z-index: -1;
}

.common-btn:hover {
  color: #408f95;
}

.common-btn span {
  position: relative;
  left: -40%;
}

.common-btn span::before {
  position: absolute;
  top: 30%;
  left: 100%;
  content: ""; /* contentプロパティを空にする */
  background-image: url("./../images/common/common-btn-img--before.png");
  background-size: 100% auto; /* 画像を幅100%、高さは自動に設定 */
  background-repeat: no-repeat; /* 画像を繰り返し表示しない */
  margin-left: 0.5rem;
  width: 2.5rem;
  height: 1.375rem;
}

.common-btn:hover span::before {
  position: absolute;
  top: 30%;
  left: 100%;
  background-image: url("./../images/common/common-btn-img--after.png");
  background-size: 100% auto; /* 画像を幅100%、高さは自動に設定 */
  background-repeat: no-repeat; /* 画像を繰り返し表示しない */
  margin-left: 0.5rem;
  width: 2.5rem;
  height: 1.375rem;
}

