.previouspostslink {
  position: relative;
  display: inline-block;
  padding: 1.125rem 1.125rem;
  border: 0.0625rem solid #408f95;
  transition: 0.3s;
}

.previouspostslink::before {
  content: "";
  width: 0.625rem;
  height: 0.625rem;
  border-top: solid 0.0625rem #408f95;
  border-right: solid 0.0625rem #408f95;
  position: absolute;
  left: 0.9375rem;
  top: 0.8125rem;
  transform: rotate(-135deg);
}

.nextpostslink {
  position: relative;
  display: inline-block;
  padding: 1.125rem 1.125rem;
  border: 0.0625rem solid #408f95;
  margin-left: 1rem;
  transition: 0.3s;
}
@media screen and (min-width: 768px) {
  .nextpostslink {
    margin-left: 2.5rem;
  }
}

.nextpostslink::before {
  content: "";
  width: 0.625rem;
  height: 0.625rem;
  border-top: solid 0.0625rem #408f95;
  border-right: solid 0.0625rem #408f95;
  position: absolute;
  left: 0.625rem;
  top: 0.75rem;
  transform: rotate(45deg);
}

.nextpostslink:hover {
  background-color: #408f95;
  transition: 0.3s;
}

.nextpostslink:hover::before {
  transition: inherit;
  border-color: #fff;
}

.previouspostslink:hover {
  background-color: #408f95;
  transition: 0.3s;
}

.previouspostslink:hover::before {
  transition: inherit;
  border-color: #fff;
}

